.toolbar-icon-admin-toolbar-tools-help::before {
  box-sizing: content-box;
  box-sizing: border-box;
  box-sizing: unset;
}
.toolbar-tray a[href] {
  color: #565656;
}
.toolbar-tray a[href]:hover,
.toolbar-tray a[href]:active,
.toolbar-tray a[href]:focus,
.toolbar-tray a[href].is-active {
  color: #000;
}
